<template>
  <div id="app">
    <ScrollTopButton/>

    <!-- Navbar start -->
    <Navbar @changeLanguage="changCurrentLanguage"/>
    <!-- Navbar end -->

    <!-- Content start -->
    <router-view v-if="isRouterAlive"></router-view>
    <!-- Content end -->

    <!-- Footer start -->
    <!-- <Footer :currentChangeLanguage="currentLanguage"/> -->
    <Footer/>
    <!-- Footer end -->
  </div>
</template>

<script>
import Navbar from './components/Navbar'
import Footer from './components/Footer'
import ScrollTopButton from './components/ScrollTopButton'
import Swal from 'sweetalert2/dist/sweetalert2.js'
import 'sweetalert2/src/sweetalert2.scss'

export default {
  name: 'App',
  components: {
    ScrollTopButton,
    Navbar,
    Footer
  },
  data(){
    return{
      isRouterAlive: true,
      currentLanguage: ''
    }
  },
  provide(){
    return{
      reload: this.reload,
      currentLang: () => this.currentLanguage
    }
  },
  methods:{
    reload () {
      console.log('reload occure')
      this.isRouterAlive = false
      // 在 DOM 已掛載和渲染完成後，自動執行 nextTick() 內的程式碼。
      // 如要做迴圈更新資料，可以等渲染完DOM後，再用nextTick()做一次性更新
      // 這樣可以減少資料重複更新與不必要的計算。
      this.$nextTick(() => {
        this.isRouterAlive = true
        window.scroll({
          top: 0,
          left: 0,
          behavior: 'smooth'
        })
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Send Email Success !!!',
          showConfirmButton: false,
          timer: 1500
        })
      }) ;
    },
    changCurrentLanguage(val){
      this.currentLanguage = val
      switch(val) {
        case 'en':
          this.$store.commit('setLang', val);
          this.$i18n.locale = val;
          localStorage.setItem('footmark-lang', val);
          break;
        default:
          this.$store.commit('setLang', val);
          this.$i18n.locale = val;
          localStorage.setItem('footmark-lang', val);
          break;
      }
    }
  }
}
</script>

<style lang="scss">
@import './assets/scss/all';

</style>
