import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    component: () => import('../pages/Index.vue'),
  },
  {
    path: '/product',
    name: 'Product',
    component: () => import('../pages/Product.vue'),
    children: [
      {
        path: 'automation',
        name: 'Automation',
        component: () => import('../pages/Automation.vue'),
        meta: {
          breadcrumbZH: [
            {
              name: "首頁",
              link: "/"
            },
            {
              name: "自動化設備"
            }
          ],
          breadcrumbEN: [
            {
              name: "Home",
              link: "/"
            },
            {
              name: "Automation"
            }
          ]
        }
      },
      {
        path: 'hydropower',
        name: 'Hydropower',
        component: () => import('../pages/Hydropower.vue'),
        meta: {
          breadcrumbZH: [
            {
              name: "首頁",
              link: "/"
            },
            {
              name: "水力發電設備"
            }
          ],
          breadcrumbEN: [
            {
              name: "Home",
              link: "/"
            },
            {
              name: "Hydropower"
            }
          ]
        }
      },
      {
        path: 'vibrationTransport',
        name: 'VibrationTransport',
        component: () => import('../pages/VibrationTransport.vue'),
        meta: {
          breadcrumbZH: [
            {
              name: "首頁",
              link: "/"
            },
            {
              name: "震動運輸設備"
            }
          ],
          breadcrumbEN: [
            {
              name: "Home",
              link: "/"
            },
            {
              name: "Vibration Transport"
            }
          ]
        }
      },
    ]
  },
  {
    path: '/contact',
    name: 'Contact',
    component: () => import('../pages/Contact.vue')
  }
]

const router = new VueRouter({
  linkActiveClass: 'active',
  routes,
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
})

export default router