import Vue from 'vue'
import App from './App.vue'
import store from "./store";

// BS 套件
import 'bootstrap'
import router from './router/index'

// i18n多語系
import VueI18n from 'vue-i18n'  // 引入 Vue I18n
import zh from './i18n/zh.json' // 存放中文語系檔
import en from './i18n/en.json' // 存放英文語系檔
Vue.use(VueI18n)

// 預設使用的語系
let locale = 'zh';

localStorage.setItem('footmark-lang', locale);

// 檢查 localStorage 是否已有保存使用者選用的語系資訊
if (localStorage.getItem('footmark-lang')) {
  locale = localStorage.getItem('footmark-lang');
  store.commit('setLang', locale);
} else {
  store.commit('setLang', locale);
}

const i18n = new VueI18n({
  locale: locale,
  messages: {
    'zh': zh,
    'en': en
  }
});

// Vue loading
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

// Vee-validation
import { ValidationObserver, ValidationProvider, extend, localize} from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
import TW from 'vee-validate/dist/locale/zh_TW.json';
import EN from 'vee-validate/dist/locale/en.json';

Object.keys(rules).forEach((rule) => {
  extend(rule, rules[rule]);
});

localize('zh_TW', TW);
localize('en', EN);

Vue.component('ValidationObserver', ValidationObserver);
Vue.component('ValidationProvider', ValidationProvider);

Vue.config.productionTip = false

Vue.component('Loading', Loading)

if (process.env.VUE_APP_ENV === "production") {
  Vue.config.devtools = false;
  Vue.config.debug = false;
  Vue.config.silent = true;
  Vue.config.productionTip = false;
}

new Vue({
  store,
  i18n,
  router,
  render: h => h(App),
}).$mount('#app')
