<template>
  <div>
    <div class="topBtn" @click="scrollTop">
      <i class="fa fa-angle-double-left" aria-hidden="true"></i>
    </div>
  </div>
</template>
<script>
// import $ from 'jquery';

export default {
  name: 'ScrollTopButton',
  data(){
    return{}
  },
  methods:{
    scrollTop(){
      window.scroll({
        top: 0,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.topBtn{
  width: 50px;
  height: 50px;
  background-color: #FCA311;
  z-index: 98;
  position: fixed;
  right: 5%;
  bottom: 5%;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0 0 0 3px rgba(#FCA311,0.5);
}

.fa-angle-double-left{
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(90deg);
  font-size: 35px;
}
</style>